import Api from '@/common/api';
let userInfo = uni.getStorageSync('userInfo');
import { mapMutations } from 'vuex';
export default {
	data() {
		return {};
	},
	methods: {
		...mapMutations(['logout']),

		navTo(url) {
			uni.navigateTo({
				url:url
			})
		},
		//退出登录
		toLogout() {
			let _this=this;
			uni.showModal({
				content: '确定要退出登录么',
				success: e => {
					if (e.confirm) {
					_this.logout();
						setTimeout(() => {
							uni.navigateBack();
						}, 200);
					}
				}
			});
		},
		//switch
		switchChange(e) {
			let statusTip = e.detail.value ? '打开' : '关闭';
			this.$api.msg(`${statusTip}消息推送`);
		}
	}
};
